<template>
    <div>

        <a-card :bordered="false">
            <h3>虚拟卡余额和消耗计算</h3>
            <a-row :gutter="20">
                <a-col :span="4">
                    <a-input-search placeholder="请输入卡号" enter-button v-model="querydata.card_number"
                    @search="findCardNumber" ></a-input-search>
                </a-col>
                <a-col :span="6">
                    <a-input-search placeholder="请输入年份（例：2024）" enter-button v-model="querydata.year"
                    @search="findCardNumber" la></a-input-search>
                </a-col>
                <a-col :span="6">
                    <a-input-search placeholder="请输入月份（例：1~12）" enter-button v-model="querydata.month"
                    @search="findCardNumber"   la></a-input-search>
                </a-col>
                <a-col :span="4">
                    <a-button type="primary" @click="findCardNumber">查询</a-button>
                </a-col>
                <a-col :span="4">
                    <span style="font-size:16px; color:#1890ff" type="primary">{{this.querydata.year==''? 'x':this.querydata.year}}年{{this.querydata.month==''? 'x':this.querydata.month}}月消耗：{{ this.month_consumption }}</span>
                </a-col>
            </a-row>

            <a-table :rowKey="(record, index) => { return index }" :columns="columns" :pagination='paginationOption'
                :dataSource="virtualCardList" bordered>
            </a-table>
        </a-card>

    </div>
</template>

<script>
//import { CloudUploadOutlined,InboxOutlined } from '@ant-design/icons-vue';
import { message } from 'ant-design-vue';
const columns = [
    {
        title: '卡号',
        dataIndex: 'card_number',
        width: '10%',
        key: 'card_number',
        align: 'center',
    },
    {
        title: '余额',
        dataIndex: 'card_balence',
        width: '15%',
        key: 'card_balence',
        align: 'center',
    },
    {
        title: '总消耗',
        dataIndex: 'total_consumption',
        width: '15%',
        key: 'total_consumptiond',
        align: 'center',

    }
]
export default {
    data() {
        return {
            labelCol: { span: 5 },
            wrapperCol: { span: 16 },
            columns,
            querydata: {
                card_number: '',
                year: '',
                month: ''
            },
            virtualCardNumber: [],
            virtualCardList: [],
            month_consumption:''
            
        }
    },
    created() {
        this.getVirtualCard()
    },
    methods: {
        async getVirtualCard() {
            const { data: result } = await this.$http.get('showVccID');
            if (result.code != 200) return this.$message.error(result.message)
            this.virtualCardNumber = result.data
            this.virtualCardNumber.map(async (item) => {
                const card_balence = await this.getCardBalence(item)
                const total_consumption = await this.getCardTotal(item)
                this.virtualCardList.push({ card_number: item, card_balence: card_balence, total_consumption: total_consumption })
            })
        },
        async getCardBalence(id) {
            const { data: result } = await this.$http.post('showVccBalance', { card_number: id });
            if (result.code != 200) return this.$message.error(result.message)
            return result.data
        },
        async getCardTotal(id) {
            const { data: result } = await this.$http.post('showVccDeplete', { card_number: id });
            if (result.code != 200) return this.$message.error(result.message)
            return result.data
        },
        async showVccBalance(id) {
            const result = await this.$http.post('showVccBalance', { card_number: id });

            if (result.status != 200) return this.$message.error(result.message)
            this.vccBalence = data.vccBalence
            this.vccVisible = true
        },
        async findCardNumber() {
            const { card_number, year, month } = this.querydata;
            if (card_number !== '' && year !== '' && month !== '') {
                const { data: result } = await this.$http.post('showVccDepleteByDate',{card_number:card_number,year:Number(year),month:Number(month)});
                if (result.code != 200) return this.$message.error(result.message)
                this.month_consumption = result.data
            }else {
                message.error('请填写完成卡号、年份、月份才可查询月消耗余额')
            }
        },
       }
    }
</script>

