<template>
    <div>

        <a-card :bordered="false">
            <h3>FaceBook文件列表</h3>
            <a-row :gutter="20">
            </a-row>
            <!-- 触发上传弹窗的按钮 -->
            <a-button type="primary" @click="showDialog" style="margin-top:10px ;">FaceBook文件导入</a-button>
            <a-modal v-model:visible="dialogVisible" title="批量导入" @ok="handleOk">
                <p>为确保导入数据格式正确，请按照格式规范录入数据</p>
                <a-upload-dragger v-model:fileList="fileList" name="f1" :multiple="true"
                    action="http://111.90.143.10:39010/api/v1/upload2" @change="handleChangeFile">
                    <p class="ant-upload-drag-icon">
                        <a-icon type="cloud-download" />
                    </p>
                    <p class="ant-upload-text">点击或将文件拖拽到这里上传</p>
                    <p class="ant-upload-hint">
                        仅支持.csv文件
                    </p>
                </a-upload-dragger>
                <div class="upload-tips">
                    <p>1.仅支持.csv文件</p>
                    <p>2.导入数量不得超过5000条</p>
                    <p>3.文件大小不得超过3M</p>
                </div>
            </a-modal>
            <a-table :rowKey="(record, index) => { return index }" :columns="columns" :pagination='paginationOption'
                :dataSource="Articlelist" bordered>
                <!-- 自定义渲染颜色列 -->
                <template slot="is_trading_authorization" slot-scope="text">
                    <!-- 使用三元运算符来根据布尔值显示不同的文本 -->
                    <span :style="{ color: !text.is_trading_authorization ? 'green' : 'red' }">
                        {{ !text.is_trading_authorization ? '是' : '否' }}
                    </span>
                </template>
            </a-table>
        </a-card>

    </div>
</template>

<script>
//import { CloudUploadOutlined,InboxOutlined } from '@ant-design/icons-vue';
import { message } from 'ant-design-vue';
const columns = [
    {
        title: 'FB账户',
        dataIndex: 'account',
        width: '10%',
        key: 'account',
        align: 'center',
    },
    {
        title: '时间',
        dataIndex: 'date',
        width: '15%',
        key: 'date',
        align: 'center',
    },
    {
        title: '交易号',
        dataIndex: 'transaction_id',
        width: '15%',
        key: 'transaction_id',
        align: 'center',

    },

    {
        title: '交易所用虚拟卡',
        dataIndex: 'payment_method',
        width: '10%',
        key: 'payment_method',
        align: 'center',
    },
    {
        title: 'order-amount',
        dataIndex: 'amount',
        width: '10%',
        key: 'amount',
        //scopedSlots: { customRender: 'img' },
        align: 'center',
    },
    {
        title: 'order-amount',
        dataIndex: 'currency',
        width: '10%',
        key: 'currency',
        //scopedSlots: { customRender: 'img' },
        align: 'center',
    },
    {
        title: '是否计算入交易授权',
        dataIndex: 'is_trading_authorization',
        width: '10%',
        key: 'is_trading_authorization',
        scopedSlots: { customRender: 'is_trading_authorization' },
        // 不需要 scopedSlots，因为我们在模板中直接定义了它  
        align: 'center',
    }

]
export default {
    data() {
        return {
            labelCol: { span: 5 },
            wrapperCol: { span: 16 },
            paginationOption: {
                pageSizeOptions: ['5', '10', '20'],
                defaultCurrent: 1,
                defaultPageSize: 5,
                showSizeChanger: true,
                showTotal:(total)=>`共${total}条`,
                onChange: (current, size) => {
                    this.paginationOption.defaultCurrent = current
                    this.paginationOption.defaultPageSize = size
                    this.getArticleList()

                },
                onShowSizeChange: (current, size) => {
                    this.paginationOption.defaultCurrent = 1
                    this.paginationOption.defaultPageSize = size
                    this.getArticleList()
                },
                total:0
            },
            Articlelist: [],
            Catelist: [],
            columns,
            querydata: {
                pagesize: 0,
                pagenum: 0,
                title: '',
            },
            dialogVisible: false, // 控制弹窗的显示  
            fileList: [], // 已上传的文件列表 
            vccBalence: 0,
            vccVisible: false,
            updateFBList: {
                transaction_id: "",
                is_ticked: false,
                note: ""
            }
        }
    },
    created() {
        this.getArticleList()
    },
    methods: {
        // 显示上传文件的弹窗  
        showDialog() {
            this.dialogVisible = true;
        },
        handleChangeFile(info) {
            const status = info.file.status;
            if (status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (status === 'done') {
                message.success(`${info.file.name} 文件上传成功！`);
            } else if (status === 'error') {
                message.error(`${info.file.name} 文件上传失败！`);
            }
        },
        handleOk(e) {
            console.log(e);
            this.dialogVisible = false;
        },
        async showVccBalance(id) {
            const result = await this.$http.post('showVccBalance', { card_number: id });
            console.log(result, '=====111')
            if (result.status != 200) return this.$message.error(result.message)
            this.vccBalence = data.vccBalence
            this.vccVisible = true
        },
        gotoEditArt(id) {
            alert(id)
            this.$router.push(`/addart/` + id)
        },
        //文章列表
        async getArticleList() {
            this.querydata.pagesize = this.paginationOption.defaultPageSize
            this.querydata.pagenum = this.paginationOption.defaultCurrent
            const url = `showfb_record?pagesize=${this.querydata.pagesize}&pagenum=${this.querydata.pagenum}`;
            const {data:result} = await this.$http.get(url);
            console.log(result, '====88')
            if (result.code != 200) return this.$message.error(result.message)
            this.Articlelist = result.data
            console.log(this.Articlelist, '=====>xihuan')
            this.paginationOption.total = result.tatal
        },
        async findArticle() {
            this.querydata.pagesize = this.paginationOption.defaultPageSize
            this.querydata.pagenum = 1
            const { data: result } = await this.$http.post('articles', this.querydata)
            if (result.status != 200) return this.$message.error(result.message)
            this.Articlelist = result.data
            this.paginationOption.total = result.total
        },
        //删除文章
        deleteArticle(id) {
            this.$confirm({
                title: "确定要删除吗？",
                content: "一旦删除，无法恢复",
                onOk: async () => {
                    const { data: result } = await this.$http.delete(`article/${id}`)
                    if (result.status != 200) return this.$message.error(result.message)
                    this.$message.success('删除成功')
                    this.getArticleList()
                },
                onCancel: () => {
                    this.$message.info("已取消删除")
                }
            })
        },
        //获取分类列表
        async getCateList() {
            const { data: result } = await this.$http.get('category')
            if (result.status != 200) return this.$message.error(result.message)
            this.Catelist = result.data
            this.paginationOption.total = result.total
        },
        //查询分类下的文章
        CateChange(value) {
            this.getCateArticle(value)
        },
        async getCateArticle(id) {
            const { data: result } = await this.$http.get(`article/cate/${id}`, {
                params: {
                    pagesize: this.paginationOption.defaultPageSize,
                    pagenum: this.paginationOption.defaultCurrent
                }
            })
            if (result.status != 200) return this.$message.error(result.message)
            this.Articlelist = result.data
            this.paginationOption.total = result.total
        },
        // 显示上传文件的弹窗  
        showDialog() {
            this.dialogVisible = true;
        },
        // // 文件上传成功时的处理  
        // handleSuccess(response, file, fileList) {
        //     console.log('上传成功:', response);
        //     // 这里可以根据实际情况进行其他处理，比如清空fileList等  
        // },
        // // 隐藏弹窗  
        // handleClose() {
        //     this.dialogVisible = false;
        //     // 可选：清空已上传的文件列表  
        //     this.fileList = [];
        // }
    },
}
</script>

<style scoped>
.actionSlot {
    display: flex;
    justify-content: center;
}
</style>
